/* buttons */
button {
  -webkit-apperance: none;
  border: 0;
  box-shadow: none;
  cursor: pointer;
}

button:focus {
  outline: none;
}

.btn {
  display: inline-block;
  position: relative;
  z-index: 1;
  text-align: center;
  transition: all 0.25s ease-in-out;
}

.btn:focus, .btn:active {
  outline: none;
}

.btn--block {
  display: block;
  width: 100%;
}

.btn-primary, .btn-more {
  overflow: hidden;
}

.btn-primary span, .btn-more span {
  position: relative;
  z-index: 2;
  transition: all 0.25s ease-in-out;
}

.btn-primary::after, .btn-more::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 10%;
  height: 10%;
  opacity: 0;
  background: currentColor;
  transition: all 0.25s ease-in-out;
}

.btn-primary:hover::after, .btn-more:hover::after {
  width: 110%;
  height: 110%;
  opacity: 1;
}

.btn-primary:hover span, .btn-more:hover span {
  color: white;
}

.btn-primary {
  padding: 9px 20px;
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 0.58px;
  text-align: center;
  background: white;
  font-family: 'Nunito', sans-serif;
  color: #656AEB;
  border-radius: 4px;
  border: 1px solid #656AEB;
}

.btn-more {
  padding: 13px 50px;
  font-size: 16px;
  text-align: center;
  font-family: 'Nunito', sans-serif;
  color: #656AEB;
  border-radius: 4px;
  border: 1px solid #656AEB;
}

.btn--filled {
  min-width: 165px;
}

.btn--filled::after {
  width: 110%;
  height: 110%;
  opacity: 1;
}

.btn--filled span {
  color: white;
}

.btn--filled:hover span {
  color: #656AEB;
}

.btn--filled:hover::after {
  width: 10%;
  height: 10%;
  opacity: 0;
}

@media only screen and (max-width: 768px) {
  .btn-primary {
    padding: 8px 20px;
    font-size: 13px;
  }
  .btn-more {
    padding: 11px 35px;
    font-size: 14px;
  }
}

/* ICONS */
.icon {
  display: inline-block;
  font-style: normal;
  content: ' ';
}

/* inherit color for svg sprite icons */
.ico {
  fill: currentColor;
}

/* logo icon is css inlined for better scale performance vs png sprite */
.icon-logo {
  background: svg-load("../icons/logo.svg") no-repeat center;
  size: 189px 32px;
  background-size: contain;
}

/* Styles for dialog window */
.mfp-wrap {
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0);
}

html.mfp-helper {
  height: 100%;
}

html.mfp-helper body {
  overflow: hidden;
  height: 100%;
  -webkit-transform: translateZ(0);
}

/* Fade-zoom animation */
.popup-buble .popup-dialog {
  opacity: 0;
  transition: all 0.2s ease-in-out;
  transform: translate3d(0, 75%, 0);
}

.popup-buble.mfp-ready .popup-dialog {
  opacity: 1;
  transform: translate3d(0, 0%, 0);
}

.popup-buble.mfp-removing .popup-dialog {
  transform: translate3d(0, 75%, 0);
  opacity: 0;
}

.popup-buble.mfp-bg {
  background: white;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.popup-buble.mfp-ready.mfp-bg {
  opacity: 1;
}

.popup-buble.mfp-removing.mfp-bg {
  opacity: 0;
}

