/* Chekbox row is required to keep checkboxes on same line */
.ui-checkbox-row {
  display: flex;
  flex-wrap: wrap;
}

/* Checkbox */
.ui-checkbox {
  margin-top: 10px;
  margin-right: 15px;
  margin-left: 10px;
}

.ui-checkbox input {
  display: none;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.ui-checkbox input:checked + label::before {
  border-color: #404994;
}

.ui-checkbox input:checked + label::after {
  background-image: svg-load("../icons/check.svg");
  background-color: transparent;
}

.ui-checkbox label {
  display: block;
  width: 100%;
  position: relative;
  cursor: pointer;
  font-size: 16px;
  color: #404994;
}

.ui-checkbox label::before {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  content: ' ';
  size: 20px;
  background-color: white;
  border: 1px solid #E7E9F0;
  box-shadow: 0 2px 1px 0 rgba(0, 0, 0, 0.03);
  border-radius: 0px;
  cursor: pointer;
  vertical-align: middle;
  cursor: pointer;
  transition: all .15s ease-in-out;
}

.ui-checkbox label::after {
  position: absolute;
  left: 6px;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  content: ' ';
  size: 10px;
  background-repeat: no-repeat;
  background-position: center center;
  background-color: rgba(64, 73, 148, 0.3);
  transition: background-image .2s ease, background-color .1s linear;
}

.ui-checkbox label span {
  display: inline-block;
  padding-left: 32px;
  vertical-align: top;
  line-height: 1.3;
  font-size: 16px;
  cursor: pointer;
}

.ui-checkbox label:hover::before {
  border-color: rgba(64, 73, 148, 0.7);
}

.ui-checkbox label:hover::after {
  background-color: rgba(64, 73, 148, 0.15);
}

