    font-size: 16px;
  }
}

/* Container with max width */
.report-container {
  max-width: 620px;
}

/* CSV */
.p-csv {
  padding-top: 15px;
  padding-bottom: 10px;
  background: white;
}

/* csv is body added class */
.csv {
  /* componenets */
}

.csv .header {
  padding-bottom: 50px;
}

.csv .page__content {
  padding-top: 120px;
}

.csv-box {
  position: relative;
  background: #F9FAFD;
  border: 1px solid #9CA2FF;
  margin: 30px 25px;
}

.csv-box__placeholder {
  position: absolute;
  top: 20%;
  left: 0;
  right: 0;
  text-align: center;
  font-family: 'Nunito', sans-serif;
  font-weight: 700;
  font-size: 138px;
  letter-spacing: 8px;
  color: #E4E5EC;
  pointer-events: none;
}

@media only screen and (max-width: 568px) {
  .csv .header {
    padding-bottom: 20px;
  }
  .csv .page__content {
    padding-top: 80px;
  }
}

.xml-code {
  overflow-x: scroll;
}

.xml-code pre, .xml-code xmp {
  margin: 0;
  padding: 20px;
}

@media only screen and (max-width: 768px) {
  .csv {
    font-size: 14px;
  }
  .csv-box {
    margin: 20px 0;
    border-left: 0;
    border-right: 0;
  }
  .xml-code pre, .xml-code xmp {
    padding: 0 15px;
  }
}
