/* FOOTER */
.footer {
  padding: 80px 0 150px 0;
  left: 0;
  right: 0;
  bottom: 0;
  &__menu {
    list-style: none;
    padding: 10px 0 25px 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #EDEEF4;
    li {
      display: inline-block;
      margin: 0 2.2%; }

    a {
      position: relative;
      font-size: 16px;
      @include nunito;
      color: $colorPrimary;
      &::after {
        display: block;
        content: ' ';
        position: absolute;
        left: 0;
        right: 100%;
        bottom: -7px;
        background: $colorPrimary;
        height: 3px;
        transition: right $transitionSpeed $ease;
        @include tr; }
      &:hover {
        &::after {
          right: 0; } } } }

  &__logo {
    margin-top: 35px;
    text-align: center; } }

/* footer responsive styles */
@include r($mobile) {
  .footer {
    padding: 70px 0 80px 0;
    &__menu {
      flex-direction: column;
      li {
        margin: 12px 0; } } } }
